import styled from "styled-components";
import { useState, useEffect, useRef } from "react";

import { smooth_left } from "../../../assets/styles/animations";
import { EngLoc } from "../../../assets/constants/localizations";
import { Title2 } from "../../../assets/styles/component";

import Toggler from "../TogglerDesktop";

const SPartnersInner = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
  margin: 0 auto;
  max-width: 100rem;

  @media (max-width: 1200px) {
    margin: 0 5%;
  }

  /* & > div > div > div {
    opacity: 0;
    ${({ view }) => !view && smooth_left}
  } */
`;

const Title = styled(Title2)`
  display: flex;
  flex-wrap: wrap;
  gap: 1.875rem;
`;

const LogosBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  background: rgba(223, 236, 253, 0.15);
  border: 2px solid rgba(241, 249, 255, 0.25);

  border-radius: 1.875rem;
  width: 9.688rem;
  height: 9.688rem;

  & img {
    max-width: 50%;
    max-height: 50%;

    &:last-child {
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0%);
      z-index: 10;
      opacity: 0;
      transition: 0.2s;
    }
  }

  opacity: 0;
  transition: ${({ timer = 0.5 }) => timer}s ease-in-out;
  ${({ active = false }) => (active ? `opacity: 1;` : `opacity: 0;`)}

  &::before {
    position: absolute;
    content: "";
    top: -2px;
    left: 0;
    width: 9.688rem;
    height: 9.688rem;
    background-color: white;
    border: 2px solid rgba(241, 249, 255, 0.25);
    border-radius: 1.875rem;
    z-index: 1;

    transition: 0.5s ease-in-out;
    opacity: 0;
  }

  &::after {
    position: absolute;
    content: "";
    top: -2px;
    left: 0;
    width: 20.6rem;
    height: 9.688rem;
    z-index: 2;
    background: ${({ img }) => "url(" + img + ")"};
    transition: 0.5s ease-in-out;
    opacity: 0;
    background-repeat: no-repeat;
    background-size: 141px auto;
    background-position: center;
    padding-right: 103px;  
  }
  
  &:hover {
    & img:last-child {
      opacity: 1;
    }

    &::after,
    &::before {
      opacity: 1;
    }

    &::before {
      width: 20.65rem;
      height: 9.688rem;
    } 
  }
`;

const GridLogos = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 1.25rem;
  justify-content: center;
  max-width: 100rem;
`;

const PartnersInner = ({ Logos, language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  const [typeBrand, setTypeBrand] = useState(0);

  return (
    <SPartnersInner ref={elem} view={view}>
      <Title>
        {language === "Ru" ? "Клиенты" : EngLoc.partners.partners_slider.title}{" "}
        <Toggler handler={setTypeBrand} language={language}></Toggler>
      </Title>
      <GridLogos>
        {Logos.map((logo, index) => (
          <LogosBox
            key={index}
            type={logo.type}
            active={typeBrand === logo.type || typeBrand === 0}
            timer={logo.timer}
            typeBrand={typeBrand}
            img={logo.text_img}
          >
            {logo.img_white}
            {logo.img}
          </LogosBox>
        ))}
      </GridLogos>
    </SPartnersInner>
  );
};

export default PartnersInner;
